import { Component, OnInit,ViewChild, ElementRef } from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';

import { AuthService } from '../services/auth.service';
import { Router ,ActivatedRoute} from '@angular/router';

import {NgbDateStruct} from '@ng-bootstrap/ng-bootstrap';
import { DatePipe } from '@angular/common'

@Component({
  selector: 'app-available-rooms',
  templateUrl: './available-rooms.component.html',
  styleUrls: ['./available-rooms.component.css']
})
export class AvailableRoomsComponent implements OnInit {
	availableRooms:any;
	configurations:any;
	capacityList:any;
	locationList:any;
	capacity:string;
	status:string;
	location:string;
	UserData:any;
	userId:string;

	userEmail:string;
	checkData:any;
	poNumber:any;
	poNumber1:any;

	configuration:string;
	startDate:any;
	endDate:any;
	minDate: any;

	startDate1:any;
	endDate1:any;
	startD:any;
	endD:any;

  model: NgbDateStruct;

  @ViewChild("myPopupclose") 
    private myPop: ElementRef;

  constructor(private authService: AuthService,
   private router: Router,
    private flashMessageService: FlashMessagesService,
    private activatedRoute: ActivatedRoute,
    private datepipe:DatePipe) {
  		this.minDate = {year:  new Date().getFullYear(), month: new Date().getMonth()+1, day: new Date().getDate()};
    
     }

	ngOnInit() {
		this.authService.getCapacity().subscribe((capacity)=>{
	      //console.log(capacity.data);
	      this.capacityList=capacity.data;
	    });
	    this.authService.getLocations().subscribe(area=>{
	        this.locationList=area.data;
	        this.location="Bangalore";
	    });
    	this.authService.getConfigurations().subscribe(configuration=>{
  			this.configurations=configuration.data;
  			// console.log(this.configurations);
  		});
	  	// this.activatedRoute.paramMap.subscribe(params => {
	       
	   //    this.location = params.get('location');
	      
	   //  });
	    if(localStorage.getItem('user')!=undefined){
	  		this.UserData=JSON.parse(localStorage.getItem('user'));
		  	//console.log(this.UserData);
		  	this.userId=this.UserData._id;
		  	this.userEmail=this.UserData.email;
		  	//this.userEmail="rr.ch447@gmail.com";
		  	//console.log(this.userEmail);
	  	}
	  	// if(localStorage.getItem('checkData')!=undefined){
	  	// 	this.checkData=localStorage.getItem('checkData');

	  	// 	this.startDate=JSON.parse(this.checkData).startDate;
	  	// 	this.endDate=JSON.parse(this.checkData).endDate;
	  	// 	this.location=JSON.parse(this.checkData).location;
	  	// 	// console.log(JSON.parse(this.checkData).startDate); 
	  	// }
	  	this.authService.getAvailableRooms().subscribe(availableRooms=>{
	  		this.availableRooms=availableRooms.data;
	  		// console.log(this.availableRooms);
	  	});
	}
	
	valuechange(newValue) {
    	this.startDate1=this.datepipe.transform(newValue.year+"-"+newValue.month+"-"+newValue.day,'yyyy-MM-dd');
  		this.endD=null;
  	}
  	valuechange1(newValue) {
  		const enddate=this.datepipe.transform(newValue.year+"-"+newValue.month+"-"+newValue.day,'yyyy-MM-dd');
  		
  		if(this.startDate1==undefined || this.startDate1>enddate){
  			this.flashMessageService.show('End date should be higher', { cssClass: 'alert-danger', timeout: 3000 });
  			setTimeout(()=>{
  				this.clear();
  			},1000)
  		}else{
  			this.startDate=this.startDate1;
  			this.endDate=enddate;
  		}
    	
  	}
  	validate(){
  		if(this.startDate==undefined || this.endDate==undefined){
			this.flashMessageService.show('please select start and end date', { cssClass: 'alert-danger', timeout: 3000 });
		}
		else{
			this.poNumber=Math.floor((Math.random() * 900000) + 100000);
    
			const emailData={
				startDate:this.startDate,
				endDate:this.endDate,
				userEmail:this.userEmail,
				poNumber:this.poNumber
			}
			console.log(emailData.poNumber);
			this.authService.sendEmail(emailData).subscribe(result=>{
				if(result.success){
					this.flashMessageService.show(result.msg, { cssClass: 'alert-success', timeout: 3000 });
				}
			})
		}
  	}
  	cancelPop(){
  		this.poNumber1="";
  		this.poNumber="";
  	}
	bookRoom(item){
		//console.log(item);
		const bookDetails={
			rmResourceId:item._id,
			userId:this.userId,
			roomId:item.roomId,
			capacity:item.capacity,
			configuration:this.configuration,
			location:item.location,
			city:item.city,
			image:item.image,
			status:"Confirmed",

			startDate:this.startDate,
			endDate:this.endDate,
			poNumber:this.poNumber
		}
		if(this.poNumber!=this.poNumber1){
			this.flashMessageService.show('please provide valid PO Number', { cssClass: 'alert-danger', timeout: 3000 });
		}else{
			//console.log(bookDetails);
			this.authService.InsertBookingRooms(bookDetails).subscribe(data=>{
				if(data.success){
					this.myPop.nativeElement.click();
	  				this.flashMessageService.show('Your Booking has Success', { cssClass: 'alert-success', timeout: 3000 });
	  				localStorage.setItem('checkData',null);
	  				this.clear(); 
	  			}
			});

			this.authService.updateBooking(bookDetails).subscribe(available=>{
				this.availableRooms=available.data;
				// localStorage.setItem('checkData',null);
			});
		}
		
  	}
  	tentativeRoom(item){
		const bookDetails={

			rmResourceId:item._id,
			userId:this.userId,
			roomId:item.roomId,
			capacity:item.capacity,
			configuration:this.configuration,
			location:item.location,
			city:item.city,
			image:item.image,
			status:"Tentative",

			startDate:this.startDate,
			endDate:this.endDate,
		}
		if(this.startDate==undefined || this.endDate==undefined){
			this.flashMessageService.show('please select start and end date', { cssClass: 'alert-danger', timeout: 3000 });
		}else{
			//console.log(bookDetails);
			this.authService.InsertBookingRooms(bookDetails).subscribe(data=>{
				if(data.success){
	  				this.flashMessageService.show('Your Booking has been Pending', { cssClass: 'alert-success', timeout: 3000 });
	  				this.clear();
	  			}
	  			else{
	  				this.flashMessageService.show('failed to insert', { cssClass: 'alert-danger', timeout: 3000 });
	  			}
			});
			this.authService.updateBooking(bookDetails).subscribe(available=>{
				this.availableRooms=available.data;
				// console.log(available);
			});
		}
			
  	}
  	clear(){
  		this.startD=null;
  		this.endD=null;
  		this.endDate=null;
  		this.startDate=null;
  		this.capacity=null;
  		this.configuration=null;
  		this.poNumber=null;
  		this.poNumber1=null;
  	}

}



