import { Component, OnInit } from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router ,ActivatedRoute} from '@angular/router';
import { AuthService } from '../services/auth.service';

import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import {FileSelectDirective,FileDropDirective,FileUploader} from 'ng2-file-upload';

//const uri='http://localhost:5000/api/users/upload';
const uri='/api/users/upload';
@Component({
  selector: 'app-room-resource',
  templateUrl: './room-resource.component.html',
  styleUrls: ['./room-resource.component.css']
})
export class RoomResourceComponent implements OnInit {
  capacityList:any;
  locationList:any;

	roomId:string;
	location:string;
	capacity:string;
	contactAddress:string;
	configuration:string=null;

	configurations:any;
	attachmentList:any=[];
	List:any=[];

  imgUploaded:string;

  LocationBasedConfig:any;
  rmInserted:string;
	public filePreviewPath: SafeUrl;
	public uploader:FileUploader = new FileUploader(
		{
			url:uri,
			maxFileSize: 2*1024*1024 
		}
	);

  constructor(private sanitizer: DomSanitizer,
  	private authService: AuthService,
   	private router: Router,
    private flashMessageService: FlashMessagesService) { 

  	this.uploader.onAfterAddingFile = (fileItem) => {
          this.filePreviewPath  = this.sanitizer.bypassSecurityTrustUrl((window.URL.createObjectURL(fileItem._file)));
        }
  	this.uploader.onBeforeUploadItem = (item) => {
      item.withCredentials = false;
    }
  	this.uploader.onCompleteItem=(item:any,response:any,status:any,headers:any)=>{
  		//console.log(JSON.parse(response));
  		this.List=[];
      	this.attachmentList.push(JSON.parse(response)); 
      	this.List=this.attachmentList;
        this.imgUploaded="Image Uploaded";
        setTimeout(()=>{
          this.imgUploaded="";
        },3000)
    }
  }

  ngOnInit() {
    this.authService.getCapacity().subscribe((capacity)=>{
        //console.log(capacity.data);
        this.capacityList=capacity.data;
      });
    this.authService.getLocations().subscribe(area=>{
          this.locationList=area.data;
          this.location="Bangalore";
    });
  	this.authService.getConfigurations().subscribe(configuration=>{
  		this.configurations=configuration.data;
  		// console.log(this.configurations);
  	});
  }
  roomInsert(){
    // this.authService.getConfigurations().subscribe(configuration=>{
    //   this.configurations=configuration.data;
    //   for(var i=0; i<this.configurations.length;i++){
    //     if(this.configurations[i].location==this.location){
    //       this.configuration=this.configurations[i].configuration;
    //       const roomDetails={
    //         roomId:this.roomId,
    //         capacity:this.capacity,
    //         location:this.location,
    //         configuration:this.configuration,
    //         contactAddress:this.contactAddress,
    //         image:this.List
    //       }
    //       if(this.roomId==undefined || this.configuration==undefined || this.contactAddress ==undefined || this.List.length==0){
    //         this.flashMessageService.show('please fill all details',{cssClass:'alert-danger', timeOut: 2000});
    //       }
    //       else{
    //         this.authService.roomResource(roomDetails).subscribe(result=>{
    //           if(result.success){
    //             this.flashMessageService.show('Room successfully inserted',{cssClass:'alert-success', timeOut: 2000});
    //             //this.clear();
    //           }
    //           else{
    //           this.flashMessageService.show('Insertion failed',{cssClass:'alert-danger', timeOut: 2000});
    //           }
    //         })
    //       }
    //     }

    //   }
    //   this.clear();
    // });
  	const roomDetails={
  		roomId:this.roomId,
  		capacity:this.capacity,
  		location:this.location,
  		contactAddress:this.contactAddress,
  		image:this.List
  	}
  	//console.log(roomDetails);
  	if(this.roomId==undefined || this.contactAddress ==undefined || this.List.length==0){
  		this.flashMessageService.show('please fill all details',{cssClass:'alert-danger', timeOut: 2000});
  	}
  	else{
  		this.authService.roomResource(roomDetails).subscribe(result=>{
  			if(result.success){
          this.rmInserted="Room successfully inserted";
          setTimeout(()=>{
            this.rmInserted="";
          },2000)
  				this.flashMessageService.show('Room successfully inserted',{cssClass:'alert-success', timeOut: 2000});
  				this.clear();
  			}
  			else{
				this.flashMessageService.show('Insertion failed',{cssClass:'alert-danger', timeOut: 2000});
  			}
  		})
  	}
  }

  clear(){
  	this.filePreviewPath="";
  	this.roomId="";
  	this.contactAddress="";
  	this.List=null;
    this.imgUploaded="";
  }

}



