import { AuthGuard } from './guards/auth.guard';
import { AuthService } from './services/auth.service';
import { ValidateService } from './services/validate.service';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { NgbModule,NgbModalModule } from '@ng-bootstrap/ng-bootstrap';

import { RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { FlashMessagesModule } from 'angular2-flash-messages';
import { HttpClientModule } from '@angular/common/http';

import { PrevBookingPipe } from './pipes/prev-booking.pipe';

import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { SearchFormComponent } from './search-form/search-form.component';
import { NavbarComponent } from './navbar/navbar.component';
import { PreviousBookingComponent } from './previous-booking/previous-booking.component';
import { GalleryComponent } from './gallery/gallery.component';
import { DownloadAppComponent } from './download-app/download-app.component';
import { RegisterComponent } from './register/register.component';
import { ProfileComponent } from './profile/profile.component';
import { BangaloreComponent } from './bangalore/bangalore.component';
import { MumbaiComponent } from './mumbai/mumbai.component';
import { HyderabadComponent } from './hyderabad/hyderabad.component';
import { ChennaiComponent } from './chennai/chennai.component';
import { NcrComponent } from './ncr/ncr.component';
import { PuneComponent } from './pune/pune.component';
import { AvailableRoomsComponent } from './available-rooms/available-rooms.component';
import { AvailableRoomPipe } from './pipes/available-room.pipe';


import { FileUploadModule } from 'ng2-file-upload';
import { DatePipe } from '@angular/common';
import { AdminComponent } from './admin/admin.component';
import { RoomResourceComponent } from './room-resource/room-resource.component';
import { ConfigurationResourceComponent } from './configuration-resource/configuration-resource.component';
import { ConfigurationPipe } from './pipes/configuration.pipe';

import { DatepickerModule, BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { KolkataComponent } from './kolkata/kolkata.component';
import { CapacityComponent } from './capacity/capacity.component';
import { LocationsComponent } from './locations/locations.component';

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    SearchFormComponent,
    NavbarComponent,
    PreviousBookingComponent,
    GalleryComponent,
    DownloadAppComponent,
    RegisterComponent,
    ProfileComponent,
    BangaloreComponent,
    MumbaiComponent,
    HyderabadComponent,
    ChennaiComponent,
    NcrComponent,
    PuneComponent,
    PrevBookingPipe,
    AvailableRoomsComponent,
    AvailableRoomPipe,
    AdminComponent,
    RoomResourceComponent,
    ConfigurationResourceComponent,
    ConfigurationPipe,
    KolkataComponent,
    CapacityComponent,
    LocationsComponent 
  ],
  imports: [
    BrowserModule,
    HttpModule,
    HttpClientModule,
    FormsModule,
    FlashMessagesModule.forRoot(),
    NgbModule.forRoot(),
    FileUploadModule,
    NgbModule.forRoot(),
    NgbModalModule.forRoot(),
    RouterModule.forRoot([
      { path: '', component: LoginComponent },
      { path: 'availability', component: SearchFormComponent, canActivate: [AuthGuard] },
      { path: 'previous-booking', component: PreviousBookingComponent, canActivate: [AuthGuard] },
      { path: 'gallery', component: GalleryComponent, canActivate: [AuthGuard] },
      { path: 'download-app', component: DownloadAppComponent, canActivate: [AuthGuard] },
      { path: 'register', component: RegisterComponent },
      { path: 'profile', component: ProfileComponent, canActivate: [AuthGuard] },
      { path: 'bangalore', component: BangaloreComponent, canActivate: [AuthGuard] },
      { path: 'mumbai', component: MumbaiComponent, canActivate: [AuthGuard] },
      { path: 'hyderabad', component: HyderabadComponent, canActivate: [AuthGuard] },
      { path: 'chennai', component: ChennaiComponent, canActivate: [AuthGuard] },
      { path: 'ncr', component: NcrComponent, canActivate: [AuthGuard] },
      { path: 'pune', component: PuneComponent, canActivate: [AuthGuard] },
      { path: 'available', component: AvailableRoomsComponent, canActivate: [AuthGuard] },
      { path: 'kolkata', component: KolkataComponent, canActivate: [AuthGuard] },
      { 
        path: 'admin', 
        component: AdminComponent, canActivate: [AuthGuard] ,
        children:[
        {
          path:'roomResource',
          component:RoomResourceComponent
        },
        {
          path:'configResource',
          component:ConfigurationResourceComponent
        }, 
        {
          path:'seater',
          component:CapacityComponent
        }, 
        {
          path:'locations',
          component:LocationsComponent
        }
      ]
     }

    ])
  ],
  providers: [AuthService, AuthGuard, ValidateService,DatePipe],
  bootstrap: [AppComponent]
})
export class AppModule { }



