import { Component, OnInit } from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';

import { AuthService } from '../services/auth.service';
@Component({
  selector: 'app-locations',
  templateUrl: './locations.component.html',
  styleUrls: ['./locations.component.css']
})
export class LocationsComponent implements OnInit {
	location:string;
	locationList:any;
  constructor(private authService: AuthService,
    private flashMessageService: FlashMessagesService) { }

  ngOnInit() {
  }
  locationInsert(){
  	const areas={
  		location:this.location
  	}
  	if(this.location==undefined){
  		this.flashMessageService.show('Please enter location',{cssClass:'alert-danger',timeout:2000});
  	}
  	else{
  		console.log(areas);
  		this.authService.InsertLocations(areas).subscribe(result=>{
  			if(result.success){
  				this.flashMessageService.show(result.msg, { cssClass: 'alert-success', timeout: 3000 });
  				//this.configuration="";
  			}
  			else{
  				this.flashMessageService.show(result.msg, { cssClass: 'alert-danger', timeout: 3000 });
  			}
  			
  		})
  	}
  }
}



