import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'prevBooking'
})
export class PrevBookingPipe implements PipeTransform {
	dataLength:number;
  transform(items: Array<any>, userId:string,status:string): any {
    if(items){
    	let data= items.filter(item=>{
    		if(userId && !status){
    			return (item.userId==userId);
    		}
    		if(userId && status){
    			return (item.userId==userId && item.status==status);
    		}
    	});
    	// this.dataLength=data.length;
    	// console.log(this.dataLength);
    	if(data.length===0){
    		return [-1];
    	}
        return data;
    }
  }

}



