import { FlashMessagesService } from 'angular2-flash-messages';
import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { ValidateService } from '../services/validate.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  name: String;
  email: String;
  password: String;

  constructor(private authService: AuthService, private validateService: ValidateService, private router: Router, private flashMessagesService: FlashMessagesService) { }

  ngOnInit() {
  }

  onRegisterSubmit() {
    const user = {
      name: this.name,
      email: this.email,
      password: this.password
    };


if(!this.validateService.validateRegister(user)){ 

      console.log('please fill all details');
      this.flashMessagesService.show('please fill all details', {cssClass:'alert-danger', timeOut: 1000});
      return false;
    }
    if(!this.validateService.validateEmail(user.email)){
      //console.log('give Proper Email..');
      this.flashMessagesService.show('please provide properMail', {cssClass:'alert-danger', timeOut: 1000});
      return false;
    }
    // Register user
    this.authService.registerUser(user).subscribe((data) => {
      this.authService.logout();
      this.router.navigate(['/']);
      this.flashMessagesService.show('Registration Succesfull. Please login', { cssClass: 'alert-success', timeout: 3000 });
    }, () => {
      this.router.navigate(['/register']);
      this.flashMessagesService.show('User already Exist.', { cssClass: 'alert-danger', timeout: 3000 });
    });
  }


}



