import { Component, OnInit ,ViewChild, ElementRef } from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';
import { DatePipe } from '@angular/common';
import { AuthService } from '../services/auth.service';
import { Router } from '@angular/router';

import * as jsPDF from 'jspdf';
@Component({
  selector: 'app-previous-booking',
  templateUrl: './previous-booking.component.html',
  styleUrls: ['./previous-booking.component.css']
})
export class PreviousBookingComponent implements OnInit {
	BookingData:any;
	UserData:any;
	userId:string;
	BookingDataLength:Number;
	status:string;
  toDay:any;

  poNumber:any
  poNumber1:any;
  userEmail:string;
  @ViewChild("modalCls") 
    public myPop: ElementRef;

  @ViewChild("content") 
    public content: ElementRef;

  constructor(private authService: AuthService,
   private router: Router,
    private flashMessageService: FlashMessagesService,
    private datePipe:DatePipe) { }

  ngOnInit() {
    this.toDay=this.datePipe.transform(new Date(),'yyyy-MM-dd');
    //console.log(this.toDay);
  	if(localStorage.getItem('user')!=undefined){
  		this.UserData=JSON.parse(localStorage.getItem('user'));
	  	//console.log(this.UserData);
	  	this.userId=this.UserData._id;
      this.userEmail=this.UserData.email;
	  	// console.log(this.userId);
  	}  	
  	this.authService.getPrevBookingData().subscribe(booking=>{
  		// console.log(booking.data);
  		this.BookingData=booking.data;
  	})
  }
  itemClick(event){
  	// console.log(event);
  	this.status=event;
  }
  validate(){
      
      this.poNumber=Math.floor((Math.random() * 900000) + 100000);
    
      const emailData={
        userEmail:this.userEmail,
        poNumber:this.poNumber
      }
      console.log(emailData.poNumber);
      this.authService.sendEmail(emailData).subscribe(result=>{
        if(result.success){
          this.flashMessageService.show(result.msg, { cssClass: 'alert-success', timeout: 3000 });
        }
      })
  }
  cancelPop(){
      this.poNumber1="";
      this.poNumber="";
  }
  downloadPO(item){
    console.log(item);
    let doc = new jsPDF();
    doc.text(80,30,"Booking confirmation");
    doc.text(15,65,"Room Id : " +item.roomId);
    doc.text(15,80,"Capacity : " +item.capacity);
    doc.text(15,95,"Dates Of Booking : " +item.startDate + "-" +item.endDate);
    if(item.configuration){
      doc.text(15,110,"Configuration : "+item.configuration);
      doc.text(15,125,"PO Number : " +item.poNumber);
    }
    else{
      doc.text(15,110,"PO Number : " +item.poNumber);
    }


    // let specialElementHandler={
    //   '#editor':function(element,renderer){
    //     return true;
    //   }
    // }
    // let content=this.content.nativeElement;
    // doc.fromHTML(content.innerHTML,15,15,{
    //   'width':190,
    //   'elementHandlers': specialElementHandler
    // })
    doc.save("bookingConfirmation"+'.pdf');
  }
  confirmBooking(item){
  	const status={
  		bookingId:item,
  		status:'Confirmed',
      poNumber:this.poNumber
  	}
    if(this.poNumber!=this.poNumber1){
      this.flashMessageService.show('Please provide valid PO Number', { cssClass: 'alert-danger', timeout: 3000 });
    }else{
      this.authService.updatePrevBookingData(status).subscribe(UpdateBooking=>{
        if(UpdateBooking.success){
          this.flashMessageService.show('Your Booking has been Confirmed', { cssClass: 'alert-success', timeout: 3000 });
          this.myPop.nativeElement.click();
        }
        this.BookingData=UpdateBooking.data;
      })
    }
  }
  cancelBooking(item){
    console.log(item);
  	const status={
  		bookingId:item._id,
      rmResourceId:item.rmResourceId,
  		status:'Cancelled',
      startDate:'',
      endDate:''
  	}
  	this.authService.updatePrevBookingData(status).subscribe(UpdateBooking=>{
  		if(UpdateBooking.success){
  			this.flashMessageService.show('Your Booking has been Cancelled', { cssClass: 'alert-danger', timeout: 3000 });
      }
  		this.BookingData=UpdateBooking.data;
  	});
    this.authService.updateBooking(status).subscribe(available=>{
        
        // localStorage.setItem('checkData',null);
    });
  }

}



