import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'configuration'
})
export class ConfigurationPipe implements PipeTransform {

  transform(items: Array<any>,location:string,capacity:string): any {
    if(items){
        let data=items.filter(item=>{
            if(location && !capacity){
              return item.location==location;
            }
            if(location && capacity){
              return (item.location==location && item.capacity==capacity);
            }
        })
        if(data.length===0){
            return [-1];
        }
        return data;
    }
  }

}



