import { Component, OnInit } from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';

import { AuthService } from '../services/auth.service';

@Component({
  selector: 'app-capacity',
  templateUrl: './capacity.component.html',
  styleUrls: ['./capacity.component.css']
})
export class CapacityComponent implements OnInit {
	capacity:string;
  constructor(private authService: AuthService,
    private flashMessageService: FlashMessagesService) { }

  ngOnInit() {
  }

  capacityInsert(){
  	const seater={
  		capacity:this.capacity
  	}
  	if(this.capacity==undefined){
  		this.flashMessageService.show('Please enter capacity',{cssClass:'alert-danger',timeout:2000});
  	}
  	else{
  		console.log(seater);
  		this.authService.InsertCapacity(seater).subscribe(result=>{
  			if(result.success){
  				this.flashMessageService.show(result.msg, { cssClass: 'alert-success', timeout: 3000 });
  				//this.configuration="";
  			}
  			else{
  				this.flashMessageService.show(result.msg, { cssClass: 'alert-danger', timeout: 3000 });
  			}
  			
  		})
  	}
  }
}



