import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service';
import {ValidateService} from '../services/validate.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  email: String;
  password: String;

  constructor(
    private authService: AuthService,
    private router: Router,
    private flashMessageService: FlashMessagesService,
    private validateService: ValidateService
  ) { }

  ngOnInit() {
    if(this.authService.loggedIn()){
      this.router.navigate(['available']);
    }
  }

  onSubmitLogin() {
    const user = {
      email: this.email,
      password: this.password
    };

if(!this.validateService.validateLogin(user)){
      //console.log('please fill all details');
 this.flashMessageService.show('please fill all details', {cssClass:'alert-danger', timeOut: 10000});
      return false;
    }
    this.authService.authenticateUser(user).subscribe(data => {
      this.authService.storeUserData(data.token, data.user);
      this.flashMessageService.show('You are now logged in', { cssClass: 'alert-success', timeout: 3000 });
      this.router.navigate(['available']);
    }, () => {
      this.flashMessageService.show('Incorrect user id or password', { cssClass: 'alert-danger', timeout: 3000 });
      this.router.navigate(['/']);
    });
  }

}



