import { Component, OnInit } from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';

import { AuthService } from '../services/auth.service';

@Component({
  selector: 'app-configuration-resource',
  templateUrl: './configuration-resource.component.html',
  styleUrls: ['./configuration-resource.component.css']
})
export class ConfigurationResourceComponent implements OnInit {
  capacityList:any;
  locationList:any;
	locations:string;
	configuration:string;
  capacity:string;
  constructor(private authService: AuthService,
    private flashMessageService: FlashMessagesService) { }

  ngOnInit() {
    this.authService.getCapacity().subscribe((capacity)=>{
      //console.log(capacity.data);
      this.capacityList=capacity.data;
    });
    this.authService.getLocations().subscribe(area=>{
        this.locationList=area.data;
        this.locations="Bangalore";
    });
  }
  configurationInsert(){
  	const config={
  		location:this.locations,
  		configuration:this.configuration,
      capacity:this.capacity
  	}
    // console.log(config);
  	if(this.configuration==undefined || this.capacity==undefined){
  		this.flashMessageService.show('please provide all the fields', { cssClass: 'alert-danger', timeout: 3000 });
  	}else{
  		this.authService.InsertConfigurations(config).subscribe(result=>{
  			if(result.success){
  				this.flashMessageService.show('Configuration Inserted', { cssClass: 'alert-success', timeout: 3000 });
  				//this.configuration="";
  			}
  			else{
  				this.flashMessageService.show(result.msg, { cssClass: 'alert-danger', timeout: 3000 });
  			}
  		});
  	}
  }

}



